import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import Tina from "../components/Tina";
import Tinashe1 from "../pictures/6.jpg";

function About() {
  return (
    <>
      <Navbar />
      <Tina cName="tina-smaller" tinaImg={Tinashe1} title="About Tinashe" />

      <h1> .. </h1>
      <h1>Overview</h1>
      <p>
        I am a huge fan of online gaming, and sometimes during class we indulge
        in a small session of PUBG with my friends but sadly I can’t say we win
        that much, lol. Personally I enjoy being around a group of people and I
        feel like good friendships contribute to leaving a healthy and happy
        life.
      </p>

      <p>
        I am not much of a sports person by I do enjoy playing tennis and I hope
        one day I will finally know how to play it properly and win, lol. The
        outdoors are always my go to place and sometimes I run to release some
        stress and maybe do a bit of crying along the way.
      </p>
      <h1> .. </h1>
      <Footer />
    </>
  );
}

export default About;
