import Tinashe2 from "../pictures/Java.png";
import Tinashe3 from "../pictures/Visual Studio.png";
import Tinashe4 from "../pictures/Coffee1.jpg";
import Tinashe5 from "../pictures/5.jpg";
import BodyInfo from "./BodyInfo";
import "./BodyStyles.css";

const Body = () => {
  return (
    <div className="body">
      <h1>Hey there!!</h1>
      <p>
        This is awesome, I hope you enjoy my page as much as I enjoyed making
        it.
      </p>

      <BodyInfo
        className="first-body"
        heading="Why I want to be a Developer at SovTech"
        text="One of the main reasons is a love for problem solving and using technology to create solutions to complex problems. Software developers have the ability to turn their ideas into tangible products that can have a significant impact on people's lives.
        Another reason is the fast pace of change and growth in the tech industry. Technology is constantly evolving, providing opportunities for developers to continuously learn and improve their skills.
        Working as a software developer can also be a very rewarding career, both financially and in terms of job satisfaction. Finally, the development process itself is often a collaborative and creative experience, with the potential to work on a wide range of projects and with a diverse team.
        In conclusion, being a developer at SovTech is so cool, I mean it’s the number one software company in Africa and is headed to the world. I would love to be part of a progressive family that is paving the way to put South Africa and Africa as a whole on the map in the tech world.
        "
        img1={Tinashe2}
        img2={Tinashe3}
      />

      <BodyInfo
        className="first-body-switch"
        heading="Brief History of my Passion"
        text="The story begins when I was 15years old, I was still a bit stupid, careless and still waiting for my first kiss, lol. I still remember the day my brother and I were playing “Need For Speed: Most Wanted” on his old PC. We were loosing like crazy and he suddenly closed the game, looked at a few videos from the internet and then did something in the files  of the game. Apparently, he increased the radius of the wheels. We started winning and from there, my passion was born. 
        I started learning from scratch using YouTube’s Bucky videos. I have been getting my hands dirty in all the programming languages I could. Now am at university and am still in pursuit for more knowledge. Learning through the education system has also been fun but it limits an individual’s imagination and creativity. I had a chance to visit the Johannesburg SovTech offices in August 2023, I couldn’t help but first notice the foosball table and everyone was so kind, it was welcoming, and I fell in love.
        "
        img1={Tinashe4}
        img2={Tinashe5}
      />
    </div>
  );
};

export default Body;
